@import '../../../scss/variables.scss';

.social-media{
    position: fixed;
    z-index: 99999;
    left: 1.5rem;
    bottom: 0rem;
    display: flex;
    flex-direction:column;
    align-items: center;
    

    .li-link{
        display: flex;
        flex-direction:column;
        align-items: center;
        margin: 2rem 0;

        .img-logo{
            width: 1.5rem;
            list-style-type:none;
            cursor: pointer;
        }
    }

    .line{
        width: 1px;
        height: 9rem;
        position: relative;
        background-color: $primary;
    }
}

.email{
    position: fixed;
    z-index: 99999;
    right: 1.5rem;
    bottom: 0rem;
    display: flex;
    align-items: center;
    justify-content:center;
    align-items: center;
    writing-mode: vertical-rl;
    text-orientation: mixed;

    .link-email{
        color: $secondary;
        margin: 2rem 0;
        text-align: center;
        cursor: pointer;
        text-decoration: none;
        letter-spacing: 0.2rem;
        font-size: 0.8rem;
    }

    .line{
        width: 1px;
        height: 9rem;
        position: relative;
        background-color: $primary;
    }
}

@media screen and (max-width:560px){

    .social-media{

        left: 0.5rem;

        .li-link{

    
            .img-logo{
                width: 1.2rem;
            }
        }
    }

    .email{
        right: 0.5rem;
    }

}